<template>
    <transition-group name="slide-down" appear>
        <div
            v-for="(error, index) in errors"
            :key="index"
            class="fvl-checkbox-error-message"
            v-html="multible + error"
        />
    </transition-group>
</template>

<script>
export default {
    props: {
        errors: {
            default: () => []
        }
    },
    computed: {
        multible() {
            return this.errors.length > 1 ? '- ' : '';
        }
    }
};
</script>
