    label="Name"
    name="name"
    type="text"
    autocomplete="name"
    :value.sync="form.name"
    placeholder="Type your name"
  />
    `,
                password: `
  <fvl-input
    label="Password"
    name="password"
    type="password"
    autocomplete="new-password"
    :value.sync="form.password"
    placeholder="Type password"
    class="w-1/2 relative"
    fieldClass="pr-8"
  >
    <!-- Optional Password Meter -->
